:root {

    --yellowcolor: #f0c808;
    --redcolor:#dd1c1a;
    --whitecolor: #f2f4f3;
    --darkbluecolor: #086788;
    --lightbluecolor: #06aed5; 

}

.contact-form {
    width: auto;
    height: auto;
    display: grid;
    grid-template-columns: auto;
    place-items: center;
}
.aboutheader h1{
    font-family: 'EB Garamond';
    color:var(--redcolor);
    font-weight: bolder;
    font-size: 45px;
}

.form-control {
    border-radius: 5px;
    padding: 5px;
    border: 1px solid grey;
    margin-top: 15px;
    box-shadow: 3px 8px 5px 0px rgba(1, 1, 1, 0.3);
    width: 650px;
    height: 40px;
    font-size: 18px;
}

.contact-msg {
    height: 110px;
}

.contact-btn {
    margin: 20px;
    width: 150px;
    height: 30px;
    color: var(--redcolor);
    background-color:var(--white);
    border: 2px solid var(--redcolor);
    border-radius: 10px;
    box-shadow: 5px 5px 0px 0px;
    font-weight: bolder;
}
.contact-btn:hover{
    color: var(--whitecolor);
    background-color: #dd1c1a;
   
    transition: 0.3s ease-in-out;
}
.contact-detailsss {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    color: black;
    margin-top: 20px;
}

.contactus-container {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    margin-top: 50px;
}

.contactus-icons {
    position: relative;
    top: 8px;
    margin-right: 5px;
    margin-left: 35px;
}

@media screen and (max-width:850px) {
    .form-control {
        width: 300px;
        font-size: 15px;
    }
    .contactus-container {
        display: grid;
        grid-template-columns: auto;
        grid-gap: 10px;
        place-items: center;
    }
    .contact-detailsss {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-top: 10px;
    }
    .contactus-icons {
        width: 20px;
    }
}