@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap');

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Titillium Web", sans-serif;
}

:root {

    --yellowcolor: #f0c808;
    --redcolor:#dd1c1a;
    --whitecolor: #f2f4f3;
    --darkbluecolor: #086788;
    --lightbluecolor: #06aed5; 

}

.navbarHeader {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 80px;
	padding: 0 2rem;
	background-color: var(--whitecolor);
	color: var(--redcolor);
	border-bottom: 2px dashed #dd1c1a;
}



.navbarHeader img{
    width: 110px;
}
.navbarHeader nav{

   position: absolute;
   right: 0;
}

nav a {
	margin: 0 1rem;
	color: var(--redcolor);
	text-decoration: none;
    font-family: 'ubuntu';
    font-weight: bolder;
    font-size: 20px;
}



nav a:hover {
	border-bottom:2px solid #dd1c1a ;
    transition: 0.2s ease-in-out;
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
}

header div,
nav {
	display: flex;
	align-items: center;
}
.navbaricons{

    margin-left: 200px;
    margin-right:50px ;
    color: var(--whitecolor);
    }

@media only screen and (max-width: 1024px) {
	header .nav-btn {
		visibility: visible;
		opacity: 1;
	}
    .navbaricons{

        margin-left: 0px;
        margin-right:0px ;
        
        }
	header nav {
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center ;
		gap: 1.5rem;
		background-color: var(--whitecolor);
		transition: 1s;
		transform: translateY(-100vh);
		z-index: 1;
		
	}

	header .responsive_nav {
		transform: none;
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
	}
}

.active{
	border-bottom: 2px solid ;
}