.footer-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: auto;
    background-color: rgb(31, 30, 30);
    flex-direction: column;
    
}

.icons {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    margin-bottom: 20px;
}

.footer-icon {
    color: white;
    margin-left: 10px;
    margin-right: 10px;
    border: 1px solid white;
    border-radius: 20px;
    padding: 6px 8px 4px 8px;
    box-shadow: 0px 5px 5px 0px rgb(255, 255, 255, 0.4);
}

hr {
    width: 65%;
}
