:root {

    --yellowcolor: #f0c808;
    --redcolor:#dd1c1a;
    --whitecolor: #f2f4f3;
    --darkbluecolor: #086788;
    --lightbluecolor: #06aed5; 

}
.menustorage{
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;
    width: 100vw;
    align-items: center;
    justify-content: center;
}
.menulist{
    display:flex;
    flex-direction:column;
    flex-wrap: wrap;
    width: 100vw;
    align-items: center;
    justify-content: center;    
    margin-top: 50px;
    background-color: #dd1c1a;
    color: var(--whitecolor);
    padding-top: 30px;
    border-radius: 50px;
    margin-left: 20px;
    margin-right: 20px;


}
.menuitems{
    display:flex;
    overflow-x: auto;
    width: 100vw;
    margin: 0px 10px;
     
}
.menuitems::-webkit-scrollbar {
    display: none;
}

.menuitem a{
    color: #dd1c1a;
}

.menuitem{
    width: auto;
    box-shadow: 3px 8px 5px 0px rgba(1, 1, 1, 0.3);
    height: auto;
    border: 2px solid  grey;
    border-radius: 50px ;
    display: flex;
    margin: 10px;
    margin-top: 30px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background-color: var(--whitecolor);
    color: #dd1c1a;
    margin-bottom: 80px;
}
.menuitem img{
    height: 200px;
    
}

.menuitem h1{
    margin: 0;
}
.menuitem a{

    margin-bottom: -20px;
}





