:root {

  --yellowcolor: #f0c808;
  --redcolor:#dd1c1a;
  --whitecolor: #f2f4f3;
  --darkbluecolor: #086788;
  --lightbluecolor: #06aed5; 

}



.homescreenslide{
  background-image: url("../images/homescreen.jpg");
  
  background-repeat: no-repeat;
  background-position:center;
  background-size:cover;
  background-color: #dd1c1a;
  
  
  
  width: 100vw;
  height: auto;
  justify-content: center;
  align-items: center;
  
  margin: 0px;
  padding:0px;
}
.homescreenslide img{
  width: 100vw;
  height: auto;
  object-fit: fill;
  background-color: #dd1c1a;
  
 

}




